.card-container p {
    margin: 0;
  }
  .card-container > .ant-tabs-card .ant-tabs-content {
    height: 120px;
    margin-top: -16px;
  }
  .card-container > .ant-tabs-card .ant-tabs-content > .ant-tabs-tabpane {
    padding: 16px;
    background: #fff;
  }
  .card-container > .ant-tabs-card > .ant-tabs-nav::before {
    display: none;
  }
  .card-container > .ant-tabs-card .ant-tabs-tab,
  [data-theme='compact'] .card-container > .ant-tabs-card .ant-tabs-tab {
    background: transparent;
    border-color: transparent;
  }
  .card-container > .ant-tabs-card .ant-tabs-tab-active,
  [data-theme='compact'] .card-container > .ant-tabs-card .ant-tabs-tab-active {
    background: #fff;
    border-color: #fff;
  }
  #components-tabs-demo-card-top .code-box-demo {
    padding: 24px;
    overflow: hidden;
    background: #f5f5f5;
  }
  [data-theme='compact'] .card-container > .ant-tabs-card .ant-tabs-content {
    height: 120px;
    margin-top: -8px;
  }

  .dark .card-container > .ant-tabs-card .ant-tabs-content {
    height: 120px;
    margin-top: -8px;
  }

  .dark .card-container > .ant-tabs-card .ant-tabs-tab {
    background: transparent;
    border-color: transparent;
  }
  .dark #components-tabs-demo-card-top .code-box-demo {
    background: #000;
  }
  .dark .card-container > .ant-tabs-card .ant-tabs-content > .ant-tabs-tabpane {
    background: #141414;
  }
  .dark .card-container > .ant-tabs-card .ant-tabs-tab-active {
    background: #141414;
    border-color: #141414;
  }

  .ant-tabs-tabpane{
    height: max-content !important;
  }
  .ant-tabs-content{
    height: max-content !important;
  }

  .dark .ant-menu-title-content{
        font-size: 1rem;
  }

  .dark .ant-tabs-tab-btn{
    font-size: 14px !important;
  }

  .dark .ant-select-selector{
    background: #151515 !important;  
  }

  #canvas{
    background: transparent;
  }

  .dark .ant-layout-header .ant-menu{
    background-color: transparent !important;
  }

  .dark .ant-layout{
    background-color: #3a3a3e;
  }

  @media print {
    .ant-select,button , .ant-switch  {
      display: none !important;
    }
  }